	module.exports = {"data":{"post":{"html":"<p>A warning for young parents: in the hands of a three-year-old, an ordinary kitchen ladle can become a deadly weapon. Christopher has been on a cooking kick lately. He likes to stir the spaghetti sauce while it is simmering, and to ladle out the portions (under strict and watchful supervision, of course). So now the ladle is his personal property, and this evening he plucked it out of the dishwasher and took it with him upstairs. Within minutes, the bathroom floor was covered with a a dangerous film of soapy water, and the planned bath was cancelled for safety reasons.</p>\n<p>He's now playing in this room with <a href=\"/2002/10/27/1956.php\">papa</a> and baby snake. How sweet.</p>","fields":{"title":"Cooking in the bathtub","date":"18 Jul 2003","url":"https://papascott.de/2003-07-18-cooking-in-the-bathtub/"}}},"pathContext":{"slug":"/2003-07-18-cooking-in-the-bathtub/","prev":{"fields":{"title":"WWII Bomb Kills 2 in Salzburg","date":"17 Jul 2003","slug":"/2003-07-17-wwii-bomb-kills-2-in-salzburg/"}},"next":{"fields":{"title":"Pilgrim Watcher","date":"18 Jul 2003","slug":"/2003-07-18-pilgrim-watcher/"}}}}

